
.index-home {
  min-height: 100vh;
}
.index-home .title1 {
  font-weight: 500;
  opacity: 0.8;
}
.slogan {
  font-size: 20px;
  line-height: 1.4;
  font-weight: 300;
}
@media screen and (min-width: 768px) {
.slogan {
    font-size: 26px;
}
}
.intro {
  min-height: 84vh;
  text-align: right;
}
@media screen and (max-width: 768px) {
.intro {
    text-align: center;
}
}
.intro .sell-phrase {
  padding-top: 12%;
}
.intro .slogan {
  padding: 32px 0 40px;
}
.intro .actions button .btn__content {
  font-weight: 700;
  font-size: 17px;
}
.intro .actions button:hover {
  background-color: #f47c47 !important;
}
.welcome {
  min-height: 80vh;
  background-color: transparent;
  background-image: url("/static/img/home/index-01.jpg");
  background-size: cover;
  background-position: center;
}
.services {
  min-height: 80vh;
  background-color: #fcfcfc;
  background-size: cover;
  background-position: center;
}
.prices {
  min-height: 50vh;
  background-size: cover;
  background-position: center;
}


