
.premium-home {
/* FF3.6-15 */
/* Chrome10-25,Safari5.1-6 */
/* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(196,138,250,0.8)), color-stop(43%, rgba(111,146,190,0.73)), color-stop(58%, rgba(111,146,190,0.7)), color-stop(65%, rgba(100,151,186,0.69)), color-stop(75%, rgba(116,166,220,0.67)), color-stop(86%, rgba(105,169,213,0.65)), color-stop(99%, rgba(98,171,211,0.63)), to(rgba(98,171,211,0.63))) !important;
  background: linear-gradient(to bottom, rgba(196,138,250,0.8) 0%, rgba(111,146,190,0.73) 43%, rgba(111,146,190,0.7) 58%, rgba(100,151,186,0.69) 65%, rgba(116,166,220,0.67) 75%, rgba(105,169,213,0.65) 86%, rgba(98,171,211,0.63) 99%, rgba(98,171,211,0.63) 100%) !important;
/* IE6-9*/
}
.premium-home .home-section {
  line-height: normal;
}
.premium-home .toolbar .toolbar__content {
  min-height: 8vh;
}
.premium-home .menu-toolbar {
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
.premium-home .menu-toolbar .toolbar__content {
  margin-left: -32px !important;
}
@media screen and (min-width: 1059px) {
.premium-home .menu-toolbar .toolbar__content {
    margin-top: -16px !important;
}
}
.premium-home .menu-toolbar .toolbar__content button {
  position: relative;
  color: #13242a;
  background: transparent;
  line-height: 1.2;
  font-size: 16px;
  text-transform: none;
}
.premium-home .menu-toolbar .toolbar__content button .btn__content {
  padding: 0 8px;
}
.premium-home .logo-toolbar .toolbar__title {
  font-size: 0px;
}
.premium-home .logo-toolbar .toolbar__title img {
  width: 200px;
}
.premium-home .logo-toolbar .toolbar__content {
  height: 64px !important;
  margin-left: -16px !important;
}
.premium-home .logo-toolbar .secondary-links {
  font-size: 13px;
  font-weight: 600;
  margin-left: 16px;
  color: #17272f;
}
.premium-home .fixed-toolbar {
  position: fixed;
  z-index: 100;
  max-width: 100%;
  background-color: #95b7d3 !important;
  padding: 8px 0;
}
.premium-home .fixed-toolbar .toolbar__content {
  padding: 0;
  margin-top: 0 !important;
}
.premium-home .fixed-logo {
  display: none;
}
.premium-home .fixed-logo-fixed {
  display: block;
  position: fixed;
  z-index: 90;
  padding-top: 84px;
}
.premium-home footer {
  background-color: #98b9d8 !important;
}
.premium-home footer .card .social {
  background-color: #000 !important;
  color: #e0e0e0 !important;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
.premium-home footer .card .social button {
  border: 1px solid;
}
.premium-home footer .card .social .copyright {
  font-size: 11px;
}
.premium-home footer .card .fixed-footer {
  position: fixed;
  bottom: 0;
}
.premium-home .go-top {
  visibility: hidden;
  -webkit-transition: all 1.5s cubic-bezier(0.25, 0.8, 0.5, 1) !important;
  transition: all 1.5s cubic-bezier(0.25, 0.8, 0.5, 1) !important;
  -webkit-transform-origin: center center 0px;
          transform-origin: center center 0px;
  border: 2px solid #95b7d4;
}
.premium-home .secondary-footer {
  background-color: #161616 !important;
}
.premium-home .secondary-footer .footer-item-container {
  max-width: 230px;
  margin: 0 auto;
}
.premium-home .secondary-footer .footer-item-container .footer-title {
  color: #76abdc;
  font-size: 14px;
  font-weight: 700;
  font-family: Lato, Montserrat, open-sans;
  line-height: 2;
}
.premium-home .secondary-footer .footer-item-container .footer-item-data {
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  padding: 2px 0;
}
.premium-home .secondary-footer .footer-item-container .footer-title-img img {
  width: 40%;
  max-height: 120px;
  margin: 0 auto;
}
.premium-home .secondary-footer .footer-link {
  font-size: 11px;
  padding: 5px 3% !important;
}


